@import "styles/colorPalette.scss";

.tableHeaderCell {
    margin: 0;
    background: $color-2 !important;
    padding-top: 5px;
    .headerFields {
        display: flex;
        align-items: center;
        .tableHeaderTitle {
            color: $color-12;
            margin-right: 8px;
            white-space: nowrap;
        }
        .tableHeaderSortIcon {
            &:hover {
                cursor: pointer;
            }
        }
    }
    .input {
        width: 100%;
    }
}
