@import "styles/colorPalette.scss";
.dialogContainer {
    width: 400px;
    height: 340px;
    .btnGroup {
        display: flex;
        justify-content: center;
        .btnCancel {
            background: $color-10;
            color: $color-1;
            margin: 0 10px;
            text-transform: none;
        }
        .btnImport {
            background: $color-9;
            color: $color-1;
            margin: 0 10px;
            text-transform: none;
        }
    }
}

.uploadBtnDiv{
    .uploadBtn {
        margin-top: 2em;
        background-color: $color-9;
        color: white;
        text-transform: initial;
        padding-left: 1.5em;
        padding-right: 1.5em;
        font-size: 0.8vw;
    }
}
