@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5.8em;
    

    .container {
        width: 100% !important;
        
        .tableContainer {
            background-color: $color-4;
            width: 100%;
        
            .table {
                width: 100%;
                background: white;
                .tableHeader {
                    padding: 10px 0;
                    background: $color-2 !important;
                    width: 100%;
                  
                    .tableHeaderCell {
                        margin: 0;
                        height: 100%;
                        background: $color-2 !important;
                        .tableHeaderTitle {
                            color: $color-12;
                            margin-right: 8px;
                        }
                        .tableHeaderSortIcon {
                            &:hover {
                                cursor: pointer;
                            }
                        }
                        .tableHeaderAutoComplete {
                      
                            width: 100%;
                            background: #f5f7fb;
                        }
                    }
                }

            }
            .updateButtonDiv {
                padding-right: 10px;
                margin-bottom: 10px;
                display: flex;
                justify-content: flex-end;
                .updateButton {
                    margin-top: 2em;
                    background-color: $color-9;
                    color: white;
                    text-transform: initial;
                    padding-left: 1.5em;
                    padding-right: 1.5em;
                    font-size: 0.8vw;
                }
            }
            .tablePagination {
                display: flex;
                justify-content: space-between;
                align-items: center;
                margin-top: 10px;
                padding-bottom: 30px;
                padding-left: 10px;
                padding-right: 10px;
                .options {
                    display: flex;
                    align-items: center;
                }
            }
        }
        .buttonsGroup {
            padding-right: 7px;
            margin-top: 8px;
            display: flex;
            justify-content: flex-end;
            .btnRed {
                margin-right: 10px;
                background-color: $color-10;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;
            }
            .btnGreen {
                background-color: $color-9;
                color: white;
                text-transform: initial;
                padding-left: 1.5em;
                padding-right: 1.5em;
                font-size: 0.8vw;
            }
        }
    }
}
