@import "../../../../Assets/colorPallete/colorPallete.scss";


.selectInput {
 width: auto;
 height: auto;

    .select {
        padding: 0;
        padding-left: 0.5em;
      }

      .input {
        width: 100%;
        height: 25px;
        border-radius: 5px;
        border: 1px solid gray;
       // padding-left: 0.5em;
        color: $color-12;
        font-weight: 900;
        background-color: white;
        font-size: 0.9vw;
        font-weight: 500;
  
        .select {
          padding: 10px;
          padding-right: 20px;
          text-align: left;
        }
        &.has-errors {
            border: 1px solid red;
          }
        &.disable {
            background-color: lightgray;
        }  
        
      }

}

.error {
    font-size: 12px;
    margin-left: 0.5em;
    margin-top: 0.5em;
    text-transform: initial;
    color: red;
}