@import "../../../Assets/colorPallete/colorPallete.scss";

.snakeRing {
    position: relative;
    width: auto;
    height: auto;
}
.snakeRing div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;

    border: 8px solid $color-4;
    border-radius: 50%;
    animation: snakeRing 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: $color-4 transparent transparent transparent;
}
.snakeRing div:nth-child(1) {
    animation-delay: -0.45s;
}
.snakeRing div:nth-child(2) {
    animation-delay: -0.3s;
}
.snakeRing div:nth-child(3) {
    animation-delay: -0.15s;
}
@keyframes snakeRing {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}
