@import "styles/colorPalette.scss";

.textField {
    width: 100%;
    height: 100%;
    padding: 0;
}

.clinicRoot {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    height: auto;

    .tableHead {
        width: 100%;
        height: auto;
        background-color: $color-2;
        -webkit-user-select: none; /* Chrome all / Safari all */
        -moz-user-select: none; /* Firefox all */
        -ms-user-select: none; /* IE 10+ */
        user-select: none;

        .tableHeadRow {
            color: $color-3;
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;

            th:nth-child(1) {
                width: 10%;
            }
            th:nth-child(2) {
                width: 20%;
            }
            th:nth-child(3) {
                width: 40%;
            }
            th:nth-child(4) {
                width: 30%;
            }

            .tableCell {
                font-size: 1vw;
                width: auto;
                height: auto;
                margin-top: 1em;

                .noTextField {
                    height: auto;
                    padding: 0;
                }
                .withTextfield {
                    padding: 0;

                    height: auto;

                    .showIcon {
                        display: inline-block;
                        position: absolute;

                        .sortIcon {
                            margin-left: 2em;
                            display: inline-block;

                            & :hover {
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }

    .tableBody {
        height: 34.5vh;
        width: 100%;
        display: flex;
        flex-direction: column;
        position: relative;
        overflow-y: scroll;
        background-color: white;

        .tableRow {
            color: $color-3;
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;
            .tableCell {
                font-size: 0.9vw;
            }

            th:nth-child(1) {
                width: 10%;
            }
            th:nth-child(2) {
                width: 20.3%;
            }
            th:nth-child(3) {
                width: 40.3%;
            }
            th:nth-child(4) {
                width: 29.4%;
            }
        }
    }
}

.rootTable {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    height: auto;

    .tableHead {
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        background-color: $color-2;
        -webkit-user-select: none; /* Chrome all / Safari all */
        -moz-user-select: none; /* Firefox all */
        -ms-user-select: none; /* IE 10+ */
        user-select: none;

        .tableHeadRow {
            color: $color-3;
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;

            th:nth-child(1) {
                width: 7.5%;
            }
            th:nth-child(2) {
                width: 15%;
            }
            th:nth-child(3) {
                width: 19%;
            }
            th:nth-child(4) {
                width: 22%;
            }
            th:nth-child(5) {
                width: 22%;
            }
            th:nth-child(6) {
                width: 16.5%;
            }

            .tableCell {
                font-size: 1vw;
                width: auto;
                height: auto;
                margin-top: 1em;

                .noTextField {
                    height: auto;
                    padding: 0;
                }
                .withTextfield {
                    padding: 0;

                    height: auto;

                    .showIcon {
                        display: inline-block;
                        position: absolute;

                        .sortIcon {
                            margin-left: 2em;
                            display: inline-block;

                            & :hover {
                                cursor: pointer;
                            }
                        }
                    }
                }
            }
        }
    }

    .tableBody {
        height: 34.5vh;
        width: 100%;
        overflow-y: scroll;
        background-color: white;
        display: flex;
        flex-direction: column;

        .tableRow {
            color: $color-3;
            width: 100%;
            height: auto;
            display: flex;
            flex-direction: row;

            .tableCell {
                font-size: 0.8vw;
            }

            th:nth-child(1) {
                width: 7.5%;
            }
            th:nth-child(2) {
                width: 15.1%;
                overflow-wrap: break-word;
            }
            th:nth-child(3) {
                width: 19%;
                overflow-wrap: break-word;
            }
            th:nth-child(4) {
                width: 22%;
                overflow-wrap: break-word;
            }
            th:nth-child(5) {
                width: 22%;
                overflow-wrap: break-word;
            }
            th:nth-child(6) {
                width: 15%;
                overflow-wrap: break-word;
            }
        }
    }
}

.paginationDiv {
    height: 40% !important;
    width: 100%;
    display: flex;

    .pageCount {
        flex: 1;
    }

    .Pagination {
        flex: 1;
        text-align: right;
        padding-right: 1em;
    }
}

.tableRow:nth-of-type(even) {
    background-color: white;
}
.tableRow:nth-of-type(odd) {
    background-color: $color-1;
}

@media screen and (max-width: 1280px) {
    .rootTable,
    .clinicRoot {
        .tableBodyRoot {
            .tableBody {
                width: 93%;
                height: 36.5vh;
                .tableBody {
                    height: 36.5vh;
                }
            }
        }
    }
}
