@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    .tableContainer {
        background: $color-4;
        outline: none;
        border: none;
        .tableButtonsGroup {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            padding: 20px;
        }
        .tableBody {
            background: $color-1;
            .tableBodyRow {
                &:hover {
                    cursor: pointer;
                }
            }
            .loadingComponent {
                width: 100%;
                height: 300px;
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }
    }
    .tableFooterButtonsGroup {
        margin-top: 20px;
        width: 100%;
        display: flex;
        justify-content: flex-end;
    }
}
