@import "styles/colorPalette.scss";

.inputFields {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 0.5em;
    color: $color-12;
    background-color: white;
}

.inputFieldsError {
    width: 100%;
    height: 30px;
    border-radius: 5px;
    border: 1px solid red;
    padding-left: 0.5em;
    color: $color-12;
    background-color: white;
}
