@import "styles/colorPalette.scss";

.dropzone {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  height: 180px;
  border-width: 2px;
  border-radius: 10px;
  // border-color: #eeeeee;
  border-style: dashed;
  background-color: #fafafa;
  color: #bdbdbd;
  outline: none;
  transition: border .24s ease-in-out;

  .active {
    border-color: $color-8;
  }

  .accept {
    border-color: $color-9;
  }

  .reject {
    border-color: $color-10;
  }

  .message {
    align-items: center;
    color: $color-4;
    margin-top: 5px;
  }

  .fileInvalidMsg {
    align-items: center;
    color: $color-10;
    margin-top: 5px;
  }

  .largeFileMsg {
    align-items: center;
    color: $color-10;
    margin-top: 15px;
  }

  span {
    font-size: 2rem;
    color: #777777;
  }

  ul {
    display: flex;
    flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
  }

  ul li {
    position: relative;
    display: block;
    padding: .55rem 1rem;
    margin-bottom: -1px;
    border: 1px solid rgba(0,0,0,.125); 
    text-align: center;
  }

  .success {
    background-color: #c3e6cb; 
    color: #155724;
  }

  .error{
    background-color: #f2dede; 
    color: #a94442;
  } 
}
