@import "styles/colorPalette.scss";

.root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 5.8em;
    flex-direction: column;
    .tableWrapper {
        background: $color-4;
        width: 100% !important;
        // margin-left: 5.8em;
        .loadingComponent {
            width: 100%;
            height: 300px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
        .table {
            width: 100%;
            height: auto;
            background: white;
            .tableHeader {
                background: $color-2 !important;
                width: 100%;
                height: auto;
                .tableHeaderCell {
                    margin: 0;
                    background: $color-2 !important;
                    .tableHeaderTitle {
                        color: $color-12;
                        margin-right: 8px;
                    }
                    .tableHeaderSortIcon {
                        &:hover {
                            cursor: pointer;
                        }
                    }
                    .tableHeaderAutoComplete {
                        width: 100%;
                        background: #f5f7fb;
                    }
                }
            }

            .tableBody {
                background: $color-1;
                .tableBodyRow {
                    &:hover {
                        cursor: pointer;
                    }
                }
                .loadingComponent {
                    width: 100%;
                    height: 300px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
            }
        }
    }

    .footer {
        display: flex;
        width: 100%;
        justify-content: flex-end;
        .btnRed {
            background: $color-10;
            margin-top: 10px;
            color: $color-1;
        }
    }
}

.tableRowStyle {
    &:hover {
        cursor: pointer;
    }
}
